body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  height: 100%;
  /* background-color: darkorange; */
}

.app * {
  /* don't allow the children of the scrollable element to be selected as an anchor node */
  overflow-anchor: none;
  /* width: 100%; */
}

#anchor {
  /* allow the final child to be selected as an anchor node */
  overflow-anchor: auto;

  /* anchor nodes are required to have non-zero area */
  height: 1px;
}

i {
  font-style: normal;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
  margin-bottom: 20px;
}

.evaluatorList > li {
  margin-bottom: 40px;
}

.evaluatorSpace, .macroSpace {
  padding-top: 10px;
  width: 93vw;
  margin-left: 6em;
  margin-bottom: 5em;
  height: 88vh;
  overflow-y: scroll
}

.evaluationHeader, .macroHeader, .noteHeader {
  /* height: 1em; */
  background-color: #0091ea;
  color: white;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding-top: 0.25em;
  padding-bottom: 0.25em;

  font-size: 1.5em;

  /* font-size: 1.5em; */
}

.evaluationHeader i {
  color: white;
}

.headerTitle {
  margin-left: 10px;
}

.macroSpace .box {
  font-size: 1.1em;
}
.prompt {
  width: 100%;
  font-size: 3em;
  border: none;
  overflow: auto;
  outline: none;
  resize: none;
  border-style: none;
  width:100%;
}

.editorContainer {
  position: fixed;
  bottom: 1px;
  width: 93vw;
  left: 6vw;
  border: 1px solid gray;
  border-radius: 5px;

  border-left: 5px solid cornflowerblue;
  border-radius: .25rem;
}

.editor {
  position: relative;
  bottom: 0;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

#editorEnter {
  vertical-align: baseline;
  font-size: 2em;
  color: grey;
  cursor: pointer;
  display: inline-block;
  padding: 12px;
}

#editorEnter:hover {
  color: #1976d2;
}

#editorEnter:active {
  color: #164b80;
}
@media (min-aspect-ratio: 1/1) {
  #topbar {
    position: fixed;
    /* top: 0; */
    left: 0;
    /* width: 5%; */
    height: 100%;
    background-color: #1976d2;
    color: white;
    text-align: center;
    /* padding-top: 1em; */
    /* padding-left: 5vw; */
  }
  
}


#input {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
  z-index: -1; 
}

.icon {
  /* min-width: 32px; */
  /* min-height: 32px; */
  color: white;
  margin: 20px;
  margin-bottom: 0px;
  cursor: pointer; /* "hand" cursor */
}

.iconLabel {
  font-size: 0.8em;
  /* margin-left: 7px; */
  margin-bottom: 20px;
}

/* Maximum aspect ratio */
@media (max-aspect-ratio: 1/1) {
  #topbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    /* height: 100%; */
    background-color: #1976d2;
    color: white;
    
    /* text-align: center; */
    /* padding-top: 1em; */
    /* padding-left: 5vw; */
  }
  #topbar p {
    display: none;
    /* display: inline-block; */
  }
  #topbar div {
    display: inline-block;
  }

  .icon {
    margin: 20px;
    /* margin-bottom: 0px; */
    cursor: pointer; /* "hand" cursor */
  }

  .evaluatorSpace, .macroSpace {
    margin-top: 5em;
    margin-left: 1vw;
  }

  .editorContainer {
    bottom: 1px;
    width: 98vw;
    left: 1vw;
  }
}

