.box {
  /* border-bottom-left-radius: 5px; */
  /* border-bottom-right-radius: 5px; */

  /* border: 1px solid black; */
  border-top: none;
  padding-left: 10px;
  padding-top: 10px;
  padding-bottom: 5px;
  font-size: 1.5em;

  border: 1px solid #eee;
  border-radius: .25rem;
  /* display: inline-block; */
}

.boxEval {
  border-left: 5px solid cornflowerblue;
}

.boxMacro {
  border-left: 5px solid darkgoldenrod;
  padding-top: 2px;
  padding-bottom: 2px;
}

.boxNote {
  border-left: 5px solid darkgreen;
  background-color: #ececec;
  /* border-top-right-radius: 5px; */
  /* border-bottom-right-radius: 5px; */
}

.inactiveStep {
  color: gray;
}

.hiddenIcon, .hiddenMacroIcon {
  margin-left: 0.75em;
  display: none;
}

li:hover > .step > .inlineblock > .hiddenIcon {
  display: inline;
  color: black;
}

li:hover .hiddenMacroIcon {
  color: black;
  /* color: grey; */
  display: inline;
}

.badge {
  padding: 2px;
  padding-left: 10px;
  padding-right: 10px;
  border: 2px solid black;
  border-radius: 1rem;
  display: inline-block;
  font-size: 0.6em;
  margin-right: 1em;
}

.makeBadge {
  cursor: pointer;
}

.makeBadge:hover {
  background-color: #BDBDBD;
}
#controls {
  margin-bottom: 10px;
}

.enabled {
  /* background: none; */
  /* border-style: none; */
	/* border-bottom: 2px solid navy; */
  /* margin-left: 5px; */
  /* margin-right: 5px;   */
  cursor: pointer;
  /* padding-left: 5px; */
}

.disabled {
  cursor: auto;
  color: grey;
}

#controls i {
  padding-left: 5px;
  padding-right: 5px;
}
.step {
  /* font-size: 2em; */
  word-wrap: anywhere;
}

.left.redex {
  background-color: #69f0ae;
  border-radius: 5px;
  /* padding: 1px; */

}

.right.redex {
  background-color: #ff80ab;
  border-radius: 5px;
  /* padding: 1px; */

}

.activeStep .macro, .activeStep .lambda, .activeStep .argument, .activeStep .churchnumeral {
  cursor: pointer;
}

.macro.redex {
  background-color: #80d8ff;
  border-radius: 5px;
  /* padding: 1px; */

}

.churchnumeral.redex {
  background-color: #82b1ff;
  border-radius: 5px;
  /* padding: 1px; */

}

.breakpoint {
  border-bottom: 3px solid black;
  /* border-radius: 5px; */
}

.application, .function, .variable, .macro, .churchnumeral {
  /* padding: 10px; */
}

.lastReduction {
  color: gray;
  font-size: 0.7em;
  margin: 0;
  margin-left: 1.5em;
}

.stepNumber {
  /* display: none; */
  color: gray;
  display: inline-block;
  margin: 0;
  font-size: 0.5em;
  margin-right: 1em;
}

.inlineblock {
  display: inline-block;
}
